.borderWelcome{border:2px dotted white;padding:3%;width:60%;margin:auto}

.userName{color:white;text-transform:to-upper-case;font-size:2em;margin-bottom:5%;font-family:"Permanent Marker", cursive}.radius{border-radius:20px}.background{background-color:#292828;padding:1%}

.task{border-radius:10px;color:#1a1919}.logged{background-color:#292828;padding:1%}.none:hover{text-decoration:none}.none:focus{outline:none}

#containerMain{background-image:url(/-quarantineApp/static/media/star.608b8372.jpg);background-repeat:no-repeat;background-size:cover;height:90vh;padding-top:10%;color:white}.gradientBackground{background:#dad0d4;background:radial-gradient(circle, #dad0d4 5%, #98878e 62%, #9d9db0 100%)}.image{height:30vh}*:hover{text-decoration:none !important}

.back {
  height: 90vh;
  background-image: url(/-quarantineApp/static/media/star.608b8372.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.position{width:100%;height:30vh}.left{display:inline-block;float:left;width:30%;height:100%}.image1{background-image:url(/-quarantineApp/static/media/ticket.e769a1f0.jpg);background-size:cover;background-repeat:no-repeat}.image2{background-image:url(/-quarantineApp/static/media/books.a7105edd.jpg);background-size:cover;background-repeat:no-repeat}.image3{background-image:url(/-quarantineApp/static/media/good.bc67cb8b.jpg);background-size:cover;background-repeat:no-repeat}.right{display:inline-block;width:70%;height:100%}

